.CastList {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-left: 12px;

  & > li {
    margin-bottom: 16px;

    & > img {
      display: block;
      width: 120px;
    }

    & > h2 {
      margin-top: 8px;
      margin-bottom: 8px;
    }

    & > p {
      margin: 0;
    }
  }
}
